// Typography style

html {
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size-adjust: .5;
}

body {
  @include default-font-size($size-body);
  font: {
    family: $base-font-family;
    weight: 400;
  };
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font: {
    family: $base-font-family;
    weight: 700;
  };
}


h1 {
  @include default-font-size($size-h1);
  @include default-margin($size-h1);
}

h2 {
  @include default-font-size($size-h2);
  @include default-margin($size-h2);
}

h3 {
  @include default-font-size($size-h3);
  @include default-margin($size-h3);
}

h4 {
  @include default-font-size($size-h4);
  @include default-margin($size-h4);
}

h5 {
  @include default-font-size($size-h5);
  @include default-margin($size-h5);
}

h6 {
  @include default-font-size($size-h6);
  @include default-margin($size-h6);
}

q,
blockquote {
  @include default-font-size($size-q);
}

@media print {
  body {
    font-size: 12pt;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    line-height: 1.25;
  }

  h1 {
    font-size: 36pt;
  }

  h2 {
    font-size: 24pt;
  }

  h3 {
    font-size: 18pt;
  }

  h4 {
    font-size: 14pt;
  }

  h5 {
    font-size: 12pt;
  }

  h6 {
    font-size: 10.5pt;
  }
  
  q,
  blockquote {
    font-size: 24pt;
    line-height: 1.4583;
  }
}
